











































































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component({
  components: {}
})
export default class InactiveTable extends Vue {
  @Prop({ required: true, type: Array, default: [] }) data!: Array<Record<string, any>>
  @Prop({ required: false, type: Object, default: {} }) query!: Record<string, any>
  @Prop({ required: false, type: Boolean, default: false }) isLoading!: boolean

  get getIndex(): number {
    return this.query.limit * (this.query.page - 1) + 1
  }

  handleSizeChange(val: number): void {
    this.$emit('sizeChange', val)
  }

  handleCurrentChange(val: number): void {
    this.$emit('currentChange', val)
  }
}
